.sidebar-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: 100vh;
    z-index: 100;
}

.sidebar {
    width: 300px;
    color: rgb(36, 36, 36);

    margin: 10.75px;
    border-radius: 15px;
    box-shadow: 0 0 12px 5px rgb(234, 234, 234);

    padding: 10px;
}

.sidebar-top {
    flex: 1;
    margin-bottom: 0;
    /* ? why does the bottom margin of this element not cover up with the margin of the top element even though stream list items behave like that */
    padding-bottom: 0;
    display: flex;
    flex-direction: column;
}


.sidebar-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    max-height: 40px;
    padding: 10px;
    border-radius: 15px;
    line-height: 40px;
    color: white;
}

.sidebar-header-service-name {
    flex: 1;
    font-size: 27px;
}

.sidebar-header-buttons {
    float: right;
}

.sidebar-stream-list {
    flex: 1;
    flex-basis: 0px;
    /* necessary to prevent the element from expanding to accomodat the (total) height of all child divs */
    overflow-y: auto;

    -ms-overflow-style: none;
    /* Hide scrollbar in Internet Explorer and Edge */
    scrollbar-width: none;
    /* Hide scrollbar in Firefox */
}

.sidebar-stream-list::-webkit-scrollbar {
    display: none;
    /* Hide scrollbar in Chrome, Safari, and Opera */
}

/* SIDEBAR BOTTOM */
.sidebar-bottom {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 45px;
}

.user_tile {
    width: 100%;
    height: 100%;
}

.user_tile-info {
    display: inline-block;
    height: 100%;
}

.user_tile-info-pfp {
    display: inline-block;
    height: 100%;
}

.user_tile-info-pfp img {
    margin-right: 10px;
    height: 45px;
}

.user_tile-info-text {
    height: 100%;
    display: inline-block;
    overflow: hidden;
    white-space: nowrap;
}

.user_tile-info-display_name {
    height: 50%;
    font-size: medium;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.user_tile-info-user_id-container{
    height: 50%;
}
.user_tile-info-user_id {
    /* height: 100%; */
    border-radius: 7px;
    padding: 5px;
    
    font-size: x-small;
    background-color: #efeeee;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    
    cursor: pointer;
}

.user_tile-info-user_id:hover {
    background-color: rgb(204, 204, 204);
}

.user_tile-buttons {
    float: right;
    margin-right: 2.5px; /* ensures equal spacing from top, button and right */
}