.note-row {
    width: 100%;
    max-width: 100%;
    margin-bottom: 10px;
}

.note-metadata-row {
    /* font-weight: bold; */
    font-size: small;
    padding: 10px 15px;
}

.note-timestamp, .note-author-name {
    display: inline-block;
}

.note-author-name {
    float:right;
}

.note-text {

    background-color: #ffffff;
    color: black;
    border-radius: 15px;
    margin: 3px 0;
    padding: 10px;

    /* Background */
    box-shadow: 0px 0px 12px 10px rgb(234, 234, 234);
}

